/**
 * Generates a range of numbers.
 * Start is inclusive, end is exclusive.
 *
 * Usage examples:
 *  range(5) === [0, 1, 2, 3, 4]
 *  range(1, 5) === [1, 2, 3, 4]
 *  range(3, 3) === []
 */
const range = (dirtyStart: number, dirtyEnd?: number): number[] => {
  const start = dirtyEnd === undefined ? 0 : dirtyStart
  const end = dirtyEnd === undefined ? dirtyStart : dirtyEnd
  const result: number[] = []

  for (let i = start; i < end; i++) {
    result.push(i)
  }

  return result
}

export default range
